import * as React from 'react';
import { Product } from '../models/Product';
import Result from './Result';

const Intimates = (products: Product[]) => (
    <div>
      <br/>
      <h2 className="text-center">Intimates</h2>
      <div className="container">
        <hr/>
        <div className="row text-center text-lg-left">
            {products["products"].map((product: Product, id = 0) => {
              if (product.category == "intimates") return <Result {...product} key={id}/>
              else return null
            })}
          </div>
      </div>
        
      </div>
  );
  
export default Intimates;
  


